import { setup as setupENS } from '../api/ens'
import SafeAppsSDK from '@gnosis.pm/safe-apps-sdk'
import { getNetwork } from '@ensdomains/ui'

const NETWORK_CHAIN_ID = {
  MAINNET: 1,
  RINKEBY: 4
}

function getLowerCase(value) {
  if (value) {
    return value.toLowerCase()
  }
  return value
}

export class SafeAppProvider {
  constructor(safe, sdk) {
    this.safe = safe
    this.sdk = sdk
  }

  get chainId() {
    return NETWORK_CHAIN_ID[this.safe.network]
  }

  sendAsync(request, callback) {
    this.send(request, callback)
  }

  send(request, callback) {
    if (!request) callback('Undefined request')
    this.request(request)
      .then(result =>
        callback(null, { jsonrpc: '2.0', id: request.id, result })
      )
      .catch(error => callback(error, null))
  }

  async request(request) {
    console.error(request.method, request.params)
    const params = request.params
    switch (request.method) {
      case 'eth_accounts':
        return [this.safe.safeAddress]

      case 'net_version':
      case 'eth_chainId':
        return `0x${this.chainId.toString(16)}`

      case 'eth_sendTransaction':
        console.error({ params })
        const tx = await this.sdk.txs.send({
          txs: params.map(tx => {
            return {
              value: '0',
              data: '0x',
              ...tx
            }
          })
        })
        return tx.safeTxHash

      case 'eth_blockNumber':
        const block = await this.sdk.eth.getBlockByNumber(['latest'])

        return block.number

      case 'eth_getBalance':
        return this.sdk.eth.getBalance([getLowerCase(params[0]), params[1]])

      case 'eth_getCode':
        return this.sdk.eth.getCode([getLowerCase(params[0]), params[1]])

      case 'eth_getStorageAt':
        return this.sdk.eth.getStorageAt([
          getLowerCase(params[0]),
          params[1],
          params[2]
        ])

      case 'eth_getBlockByNumber':
        return this.sdk.eth.getBlockByNumber([params[0], params[1]])

      case 'eth_getBlockByHash':
        return this.sdk.eth.getBlockByHash([params[0], params[1]])

      case 'eth_getTransactionByHash':
        let txHash = params[0]
        try {
          const resp = await this.sdk.txs.getBySafeTxHash(txHash)
          txHash = resp.transactionHash || txHash
        } catch (e) {}
        return this.sdk.eth.getTransactionByHash([txHash]).then(transaction => {
          if (transaction) {
            transaction.hash = params[0]
          }
          return transaction
        })

      case 'eth_getTransactionReceipt': {
        let txHash = params[0]
        try {
          const resp = await this.sdk.txs.getBySafeTxHash(txHash)
          txHash = resp.transactionHash || txHash
        } catch (e) {}
        return this.sdk.eth
          .getTransactionReceipt([txHash])
          .then(transaction => {
            if (transaction) {
              transaction.transactionHash = params[0]
            }
            return transaction
          })
      }

      case 'eth_estimateGas': {
        return 0
      }

      case 'eth_call': {
        return this.sdk.eth.call([params[0], params[1]])
      }

      case 'eth_getLogs':
        return this.sdk.eth.getPastLogs([params[0]])

      default:
        throw Error(`"${request.method}" not implemented`)
    }
  }
}

const safeAppsSdk = new SafeAppsSDK()

export const safeInfoWithTimeout = async () => {
  try {
    return await Promise.race([
      safeAppsSdk.getSafeInfo(),
      new Promise(resolve => setTimeout(resolve, 100))
    ])
  } catch (e) {
    return undefined
  }
}

export const setupSafeApp = async safeInfo => {
  const safe = safeInfo || (await safeApp.getSafeInfo())
  const provider = new SafeAppProvider(safe, safeAppsSdk)
  await setupENS({
    customProvider: provider,
    reloadOnAccountsChange: true,
    enforceReload: true
  })
  return await getNetwork()
}
